%h1, .h1,
h1 {
  color: $color-primary;
  font-family: $font-primary;
  font-size: rem(45px);
  font-weight: 800;
  line-height: 1.5;

  @include from(m) {
    font-size: rem(54px);
  }

  @include from(xl) {
    font-size: rem(64px);
  }
}

%h2, .h2,
h2 {
  @extend %h1;
  font-size: rem(28px);
  line-height: 1.4;

  @include from(m) {
    font-size: rem(36px);
  }

  @include from(xl) {
    font-size: rem(45px);
  }
}

%h3, .h3,
h3 {
  @extend %h1;
  font-size: rem(20px);
  line-height: 1.4;

  @include from(m) {
    font-size: rem(22px);
  }

  @include from(xl) {
    font-size: rem(32px);
  }
}

%title,
.title {
  @extend %h1;
  font-size: rem(22px);
  line-height: 1.5;

  @include from(m) {
    font-size: rem(25px);
  }

  @include from(xl) {
    font-size: rem(30px);
  }
}

%title-2,
.title-2 {
  @extend %title;
  font-size: rem(18px);
  line-height: 1.4;

  @include from(m) {
    font-size: rem(20px);
  }

  @include from(xl) {
    font-size: rem(22px);
  }
}

%title-3,
.title-3 {
  @extend %title;
  font-size: rem(16px);
  line-height: 1.4;

  @include from(m) {
    font-size: rem(18px);
  }

  @include from(xl) {
    font-size: rem(20px);
  }
}


%highlight,
.highlight {
  font-family: $font-secondary;
  font-size: rem(16px);
  line-height: 1.4;

  @include from(m) {
    font-size: rem(18px);
  }

  @include from(xl) {
    font-size: rem(20px);
  }
}

%body {
  @extend %highlight;
  color: $color-primary;
  font-size: rem(14px);
  font-weight: normal;
  line-height: 1.4;

  @include from(m) {
    font-size: rem(16px);
  }

  @include from(xl) {
    font-size: rem(18px);
  }
}

%text-button {
  @extend %h1;
  font-size: rem(16px);
  font-weight: bold;
}

p,
ul,
ol,
div {
  @extend %body;
}

strong,
b {
  font-weight: bold;
}

a {
  @extend %h2;
  font-size: rem(16px);
  font-weight: bold;
  text-decoration: none;
  &:hover {
    color: $magenta;
  }
}
