// Colors
$black: #000000;
$blue:#1932D7;
$blue-dk:#281E64;
$blue-lt:#D5DAFD;
$degrade: linear-gradient(90deg, #C800FF 0%, #6400FF 100%);
$magenta:#C800FF;
$purple: #6400FF;
$white: #ffffff;

// Brand Colors
$color-primary: $blue;
$color-secondary:$magenta;

// Fonts
$font-primary: 'Nunito', sans-serif;
$font-secondary: 'Open Sans', sans-serif;

// header utils
$header_height_m: 200px;
$header_height_l: 174px;

// Media queries
$breakpoints-min: (
  's': 576px,
  'm': 768px,
  'l': 992px,
  'xl': 1470px,
);

$breakpoints-max: ('xs': map-get($breakpoints-min, 's') - 1px,
  's': map-get($breakpoints-min, 'm') - 1px,
  'm': map-get($breakpoints-min, 'l') - 1px,
  'l': map-get($breakpoints-min, 'xl') - 1px,
);

$container-max-widths: ('s': 540px,
  'm': 720px,
  'l': 960px,
  'xl': 1320px,
);
